.search-form {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  border: none;
  border-bottom: 2px solid #ccc;
  border-radius: 5px 0 0 5px;
  outline: none;
  flex: 1;
  padding: 10px;
  transition: border-color .3s;
}

.search-form input[type="text"]:focus {
  border-color: #6c63ff;
}

.search-form .search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #6c63ff;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
  transition: background-color .3s;
}

.search-form .search-btn:hover {
  background-color: #4f46e5;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: 300px;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: 50%;
}

.info {
  background-color: #f7f7f7;
  padding: 10px;
}

.info-item {
  margin-bottom: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

